@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
@import url(https://fonts.googleapis.com/css?family=Hind+Vadodara);
@import url(https://fonts.googleapis.com/css?family=Neucha);
body {
  margin: 0;
  padding: 100px 0 0 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  outline: 0;
}

@media only screen and (max-width: 768px) {
  body {
    padding: 10px 0 0 0;
  }
}
.App {
  font-family: 'Hind Vadodara', sans-serif;
  font-weight: 400;
}

.content {
  float: left;
  width: calc(100% - 250px);
  margin-left: 250px;
  z-index: 5;
  background-color: white;
}

@media only screen and (max-width: 768px) {
  .content {
    width: 100%;
    margin-left: 0px;
  }
}





.header-item {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 15px;
  font-weight: 500;
  text-transform: uppercase;
  position: relative;
  top: 50%;
  z-index: 0;
  cursor: pointer;
}

.menu {
  cursor: pointer;
}


.header-logo {
  padding-right: 47px;
}

.header-item a, a:hover {
  color: #424242;
  text-decoration: none;
}

@media only screen and (max-width: 768px) {
  .header {
    display: block !important;
  }
}



.sidebar {
  position: fixed;
  width: 250px;
}

.logo img {
  image-rendering: crisp-edges;
}

.sidebar-item {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 17px;
  font-weight: 500;
  position: relative;
  top: 50%;
  cursor: pointer;
}

.sidebar-item {
  color: #424242;
  text-decoration: none;
}

.active-sidebar-item .sidebar-item, .active-sidebar-item:hover .sidebar-item {
  color: #e0b113;
}


.copyright {
  position: fixed;
  width: 250px;
  bottom: 0;
}

@media only screen and (max-width: 768px) {
  .sidebar {
    display: none !important;
  }
}

.art-container {
  display: grid;
  padding: 0px 80px;
  grid-template-columns: 50% 50%;
}

.artwork-element {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  cursor: pointer;
}

.image-title {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize;
  color: #333;
}

.art-image {
  opacity: 1;
  transition: opacity 0.4s ease;
  max-width: 600px;
  max-height: 600px;
}

.art-image:hover {
  opacity: 0.75;
  transition: opacity 0.5s ease;
}

p.image-title {
  opacity: 0;
  transition: opacity 0.4s;
}

.artwork-element:hover p.image-title {
  opacity: 1;
  transition: opacity 0.4s;
}

.art-container,
.art-container img {
  z-index: 99;
}

@media only screen and (max-width: 1600px) {
  .art-image {
    max-width: 500px;
    max-height: 500px;
  }
}

@media only screen and (max-width: 1400px) {
  .art-image {
    max-width: 600px;
    max-height: 600px;
  }
  .art-container {
    grid-template-columns: 100%;
  }
}

@media only screen and (max-width: 1200px) {
  .art-image {
    max-width: 500px;
    max-height: 500px;
  }
}

@media only screen and (max-width: 992px) {
  .art-image {
    max-width: 400px;
    max-height: 400px;
  }
}

@media only screen and (max-width: 480px) {
  .art-image {
    max-width: 300px;
    max-height: 300px;
  }

  p.image-title {
    opacity: 1 !important;
  }
}

.about-text {
  background-color: white;
  z-index: 99;
}

.row {
  margin: 0px 0px;
}
.contact{
  text-align: left;
}

.contact h3.info-label {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: black;
  text-transform: uppercase;
}

.contact p {
  font-family: 'Hind Vadodara', sans-serif;
  color: #333;
}



.fa {
  position: relative;
  padding: 20px 20px;  
  text-align: center;
}

.fa-instagram{
  color: #810000;
}

.fa-leaf {
  color: #004225;
}

.fa-behance {
  color: #002366;
}

.description {
  background-color: white;
  z-index: 9999;
}

.description-text {
  opacity: 1;
  background-color: #fff;
  transition: opacity 1s ease;
}

.description-text-hidden {
  opacity: 0;
  transition: opacity 1s ease;
}

.artwork-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.artwork-container img {
  max-width: 800px;
  max-height: 800px;
  z-index: 9999;
  margin-bottom: 40px;
}

@media only screen and (max-width: 1200px) {
  .artwork-container img {
    max-width: 680px;
  }
}

@media only screen and (max-width: 992px) {
  .artwork-container img {
    max-width: 450px;
  }
}

@media only screen and (max-width: 768px) {
  .artwork-container img {
    max-width: 500px;
  }
}



@media only screen and (max-width: 576px) {
  .artwork-container img {
    max-width: 300px;
  }
}
