.art-container {
  display: grid;
  padding: 0px 80px;
  grid-template-columns: 50% 50%;
}

.artwork-element {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  cursor: pointer;
}

.image-title {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize;
  color: #333;
}

.art-image {
  opacity: 1;
  transition: opacity 0.4s ease;
  max-width: 600px;
  max-height: 600px;
}

.art-image:hover {
  opacity: 0.75;
  transition: opacity 0.5s ease;
}

p.image-title {
  opacity: 0;
  transition: opacity 0.4s;
}

.artwork-element:hover p.image-title {
  opacity: 1;
  transition: opacity 0.4s;
}

.art-container,
.art-container img {
  z-index: 99;
}

@media only screen and (max-width: 1600px) {
  .art-image {
    max-width: 500px;
    max-height: 500px;
  }
}

@media only screen and (max-width: 1400px) {
  .art-image {
    max-width: 600px;
    max-height: 600px;
  }
  .art-container {
    grid-template-columns: 100%;
  }
}

@media only screen and (max-width: 1200px) {
  .art-image {
    max-width: 500px;
    max-height: 500px;
  }
}

@media only screen and (max-width: 992px) {
  .art-image {
    max-width: 400px;
    max-height: 400px;
  }
}

@media only screen and (max-width: 480px) {
  .art-image {
    max-width: 300px;
    max-height: 300px;
  }

  p.image-title {
    opacity: 1 !important;
  }
}
